import { injectIntl, IntlShape } from 'react-intl';

import { localeLogo } from './locale/localeLogo';

type Props = {
  intl: IntlShape;
  firstWordColor: string;
  secondWordColor: string;
};
const Logo = ({ intl: { formatMessage }, firstWordColor, secondWordColor }: Props) => {
  const kirayTitle = formatMessage(localeLogo.logoText.kirayTitle);
  const kifiyaTitle = formatMessage(localeLogo.logoText.kifiyaTitle);
  return (
    <div className="logo-container">
      <span style={{ color: firstWordColor }} className="title">
        {kirayTitle}
      </span>
      <span style={{ color: secondWordColor }} className="title">
        {kifiyaTitle}
      </span>
    </div>
  );
};

export default injectIntl(Logo);
